<template>
  <div id="courses" class="container">
    <sequential-entrance fromBottom>
      <div class="courses">
        <h2>Our Featured Courses</h2>
        <p>
          Through our state-of-the-art facility, our virtual attendees
          participate in discussions with the classroom students and Course
          Director(s).
        </p>

        <div class="courses_list">
          <div
            class="courses_item"
            v-for="(course, num) in coursesList"
            :key="num"
          >
            <div class="image"><img :src="course.image" alt="image" /></div>
            <div class="courses_desc">
              <div class="date">{{ course.date }}</div>
              <h3 class="head">{{ course.name }}</h3>
              <div class="text">{{ course.text }}</div>
              <div class="students_qty">
                {{ course.students }} students enrolled
              </div>
            </div>
          </div>
        </div>
      </div>
    </sequential-entrance>
  </div>
</template>

<script>
import axios from "axios";
export default {
  name: "Courses",
  data() {
    return {
      coursesList: [],
    };
  },
  mounted() {
    const url = "./courses.json";
    axios
      .get(url)
      .then((resp) => {
        this.coursesList = resp.data;
      })
      .catch((error) => {
        console.log(error);
      });
  },
};
</script>
